body{
  margin: 0;
  background-color:rgb(39, 110, 158);
  font-family: sans-serif;
}
.todo-container
{
    width: 350px;
    /* position: absolute;
    left: 50%;
    top:50%; 
    transform: translate(-50%,-50%);*/
    margin: auto;
    margin-top: 30px;;
    text-align: center;
    
}
h1
{
  margin: 0em;
  margin-bottom:15px;  
  color: aliceblue;
}
.input-section
{
  background-color: rgb(0, 209, 236);
  padding: 18px;
  border-radius: 10px;

}

.input-section input{
  border-radius: 5px;
  border: 1px solid black ; 
  width: 100%;
  padding: 10px;
  box-sizing: border-box; 
}
.input::placeholder
{
  color: silver;
}
ul{
  text-align: left;
  padding: 0%;

}
ul li{
  display: flex;
   list-style: none;
  background-color: white;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 10px;
  color: rgb(39, 110, 158);
  
}
ul li i
{
  text-align: right;
  flex: 1;
  cursor: pointer;
}